:root {
    --main-bg: #c2c2c2;
  }

.div{
    height: 100vh;
    width: 100vw;
}

.img {
    display: block;
    margin: auto;    
    padding-top: 100px;
    
    animation: shake-baidu 1s ease 0s infinite;
}

  .img:hover {
      animation: shake-baidu 0.82s cubic-bezier(.36,.07,.19,.97) alternate;
      cursor: pointer;
  }
  
  .img:target{
      transform: scale(0.1);
  }
  @keyframes shake-baidu {
    0% { transform: rotate(0deg); }
   80% { transform: rotate(0deg); }
   85% { transform: rotate(5deg); }
   95% { transform: rotate(-5deg); }
  100% { transform: rotate(0deg); }

  }