@media only screen and (max-width: 850px) {
  .logo,
  .nav {
    display: none;
  }
  input.btn {
    display: none;
  }
  .hamburger {
    position: fixed;
    display: block;
    background-color: transparent;
    left: 0;
    top: 0;
    height: 30px;
    width: 30px;
    padding: 20px 20px;
    -webkit-transform: translate3d(0, 0, 0);
    transform: translate3d(0, 0, 0);
    transition: -webkit-transform 0.25s cubic-bezier(0.05, 1.04, 0.72, 0.98);
    transition: transform 0.25s cubic-bezier(0.05, 1.04, 0.72, 0.98);
    transition: transform 0.25s cubic-bezier(0.05, 1.04, 0.72, 0.98), -webkit-transform 0.25s cubic-bezier(0.05, 1.04, 0.72, 0.98);
    z-index: 1002;
    cursor: pointer;
    -webkit-user-select: none;
    -ms-user-select: none;
    user-select: none;
  }
  .hamburger.is-active {
    background-color: none;
  }
  ._layer {
    background: #000000;
    margin-bottom: 4px;
    border-radius: 2px;
    width: 28px;
    height: 4px;
    opacity: 1;
    -webkit-transform: translate3d(0, 0, 0);
    transform: translate3d(0, 0, 0);
    transition: all 0.25s cubic-bezier(0.05, 1.04, 0.72, 0.98);
  }
  .hamburger:hover .-top {
    -webkit-transform: translateY(-100%);
    transform: translateY(-100%);
  }
  .hamburger:hover .-bottom {
    -webkit-transform: translateY(100%);
    transform: translateY(100%);
  }
  .hamburger.is-active .-top {
    -webkit-transform: translateY(200%) rotate(45deg) !important;
    transform: translateY(200%) rotate(45deg) !important;
  }
  .hamburger.is-active .-mid {
    opacity: 0;
  }
  .hamburger.is-active .-bottom {
    -webkit-transform: translateY(-200%) rotate(135deg) !important;
    transform: translateY(-200%) rotate(135deg) !important;
  }

  .menuppal.is_active {
    -webkit-transform: translate3d(0px, 0px, 0px);
            transform: translate3d(0px, 0px, 0px);
  }
  .menuppal {
    background-color: rgba(145, 145, 145, 0.95);
    bottom: 0;
    height: 100%;
    left: 0;
    overflow-y: scroll;
    position: fixed;
    top: 0;
    -webkit-transform: translate3d(0px, -100%, 0px);
            transform: translate3d(0px, -100%, 0px);
    transition: -webkit-transform 0.35s cubic-bezier(0.05, 1.04, 0.72, 0.98) 0s;
    transition: transform 0.35s cubic-bezier(0.05, 1.04, 0.72, 0.98) 0s;
    transition: transform 0.35s cubic-bezier(0.05, 1.04, 0.72, 0.98) 0s, -webkit-transform 0.35s cubic-bezier(0.05, 1.04, 0.72, 0.98) 0s;
    width: 100%;
    z-index: 1001;
  }
  .is_active .btn,
  .is_active .logo {
    display: block;
    margin: 2px;
    color: #fff;
    border: solid 1px #343434;
    border: solid 1px var(--main-bg-color);
    border-radius: 5px;
    background-color: #545454;
    -webkit-filter: brightness(0.9);
            filter: brightness(0.9);
  }
  .filterContainer {
    padding-top: 58px;
    padding-left: 20px;
  }
  .navbar > .is_active {
    display: block;
  }
  ul.is_active {
    right: 10px;
    color: #fff;
    background-color: #545454;
    -webkit-filter: brightness(0.9);
            filter: brightness(0.9);
    border-radius: 5px;
    margin: 0 10px;
    position: absolute;
    display: block;
    z-index: 1001;
  }
  ul.is_active:hover {
    border-bottom-left-radius: 0;
    border-bottom-right-radius: 0;
  }
  .nav > li:hover > ul {
    position: absolute;
    top: 25px;
  }
  .backToHome{
      display: inline-block;
  }
}

